import React from "react";

export default function EventsGrid() {
  const events = [
    {
      type: "EVENT",
      title: "Annual fest that celebrates everything JSS stands for",
      date: "August 16, 2024",
      bgColor: "#C84B3A",
    },
    {
      type: "NEWS",
      title: "SmashZone League: The Ultimate Badminton Battle",
      date: "October 04, 2024",
      bgColor: "#9B9B9B",
    },
    {
      type: "EVENT",
      title: "Smart Energy Council: Powering Australia's Renewable Future",
      date: "October 11, 2024",
      logo: true,
      bgColor: "#FFFFFF",
    },
    {
      type: "UPCOMING EVENTS",
      title: 'TECHTONIC SUMMIT: IDEAS THAT SHAKE THE FUTURE"',
      bgColor: "#4A4A4A",
      isLarge: true,
      img: "/images/home-page/seventh-section-banner.png",
    },
    {
      type: "EVENT",
      title: "Business Today CODESTORM 2.0",
      subtitle: "A tech showdown with coding battles and hackathons",
      date: "October 16, 2024",
      bgColor: "#2B5DAA",
    },
    {
      type: "EVENT",
      title: "Hosts the 18th International Olympiad on Astronomy",
      date: "October 16, 2024",
      logo: "seamedu",
      bgColor: "#E8E8E8",
    },
    {
      type: "UPCOMING",
      title: "SUMMER BEATS FESTIVAL 2025",
      subtitle:
        "A fusion of tech and music, featuring DJ sets and app showcases",
      bgColor: "#4A4A4A",
      isLarge: true,
    },
  ];

  return (
    <div style={styles.container}>
      <div style={styles.header}>
        <p style={styles.headerSmall}>HAPPENINGS</p>
        <h1 style={styles.headerTitle}>
          WHAT'S <span style={styles.highlight}>HAPPENING</span> @JSS NOIDA
        </h1>
      </div>

      <div style={styles.grid}>
        {/* Large Event 1 */}
        <div
          style={{
            ...styles.card,
            ...styles.cardLarge,
            backgroundColor: events[3].bgColor,
          }}
        >
          <p style={styles.cardTag}>{events[3].type}</p>
          <h2 style={styles.cardTitleLarge}>{events[3].title}</h2>
          <div style={styles.iconButton}>→</div>
        </div>

        {/* Event 1 */}
        <div style={{ ...styles.card, backgroundColor: events[0].bgColor }}>
          <p style={{ ...styles.cardTag, color: "#fff" }}>{events[0].type}</p>
          <h3 style={{ ...styles.cardTitle, color: "#fff" }}>
            {events[0].title}
          </h3>
          <p style={{ ...styles.cardDate, color: "#fff" }}>{events[0].date}</p>
        </div>

        {/* News 1 */}
        <div style={{ ...styles.card, backgroundColor: events[1].bgColor }}>
          <p style={{ ...styles.cardTag, color: "#fff" }}>{events[1].type}</p>
          <h3 style={{ ...styles.cardTitle, color: "#fff" }}>
            {events[1].title}
          </h3>
          <p style={{ ...styles.cardDate, color: "#fff" }}>{events[1].date}</p>
        </div>

        {/* Event 2 */}
        <div style={{ ...styles.card, backgroundColor: events[2].bgColor }}>
          <p style={styles.cardTag}>{events[2].type}</p>
          <div style={styles.logoContainer}>
            <div style={styles.logo}>
              <div
                style={{ ...styles.triangle, borderLeft: "15px solid #FFB800" }}
              ></div>
              <div
                style={{ ...styles.triangle, borderLeft: "15px solid #0066CC" }}
              ></div>
            </div>
          </div>
          <h3 style={styles.cardTitle}>{events[2].title}</h3>
          <p style={styles.cardDate}>{events[2].date}</p>
        </div>

        {/* Event 3 */}
        <div style={{ ...styles.card, backgroundColor: events[4].bgColor }}>
          <p style={{ ...styles.cardTag, color: "#fff" }}>{events[4].type}</p>
          <h3 style={{ ...styles.cardTitle, color: "#fff", fontSize: "20px" }}>
            <span style={{ color: "#6FB3FF" }}>→</span> Business Today
            <br />
            CODESTORM 2.0
          </h3>
          <p style={{ ...styles.cardSubtitle, color: "#fff" }}>
            {events[4].subtitle}
          </p>
          <p style={{ ...styles.cardDate, color: "#fff" }}>{events[4].date}</p>
        </div>
        {/* Large Event 2 */}
        <div
          style={{
            ...styles.card,
            ...styles.cardLarge,
            backgroundColor: events[6].bgColor,
          }}
        >
          <div style={styles.orangeLine}></div>
          <h2 style={styles.cardTitleLarge}>{events[6].title}</h2>
          <p
            style={{ ...styles.cardSubtitle, color: "#fff", marginTop: "10px" }}
          >
            {events[6].subtitle}
          </p>
          <div style={styles.iconButton}>→</div>
        </div>
        {/* Event 4 */}
        <div
          style={{
            ...styles.card,
            backgroundColor: events[5].bgColor,
            position: "relative",
          }}
        >
          <div style={styles.playButton}>▶</div>
          <p style={styles.cardTag}>{events[5].type}</p>
          <div style={styles.seamEduLogo}>
            <span style={{ fontWeight: "bold", fontSize: "18px" }}>
              Knowledge Partner
            </span>
            <span style={{ fontSize: "24px", fontWeight: "bold" }}>
              seam<span style={{ fontWeight: "300" }}>edu</span>
            </span>
          </div>
          <h3 style={styles.cardTitle}>{events[5].title}</h3>
          <p style={styles.cardDate}>{events[5].date}</p>
        </div>
        {/* Event 4 */}
        <div
          style={{
            ...styles.card,
            backgroundColor: events[5].bgColor,
            position: "relative",
          }}
        >
          <div style={styles.playButton}>▶</div>
          <p style={styles.cardTag}>{events[5].type}</p>
          <div style={styles.seamEduLogo}>
            <span style={{ fontWeight: "bold", fontSize: "18px" }}>
              Knowledge Partner
            </span>
            <span style={{ fontSize: "24px", fontWeight: "bold" }}>
              seam<span style={{ fontWeight: "300" }}>edu</span>
            </span>
          </div>
          <h3 style={styles.cardTitle}>{events[5].title}</h3>
          <p style={styles.cardDate}>{events[5].date}</p>
        </div>
        {/* Event 4 */}
        <div
          style={{
            ...styles.card,
            backgroundColor: events[5].bgColor,
            position: "relative",
          }}
        >
          <div style={styles.playButton}>▶</div>
          <p style={styles.cardTag}>{events[5].type}</p>
          <div style={styles.seamEduLogo}>
            <span style={{ fontWeight: "bold", fontSize: "18px" }}>
              Knowledge Partner
            </span>
            <span style={{ fontSize: "24px", fontWeight: "bold" }}>
              seam<span style={{ fontWeight: "300" }}>edu</span>
            </span>
          </div>
          <h3 style={styles.cardTitle}>{events[5].title}</h3>
          <p style={styles.cardDate}>{events[5].date}</p>
        </div>
      </div>
    </div>
  );
}

const styles = {
  container: {
    padding: "40px 20px",
    maxWidth: "1200px",
    margin: "0 auto",
    fontFamily: "Arial, sans-serif",
  },
  header: {
    textAlign: "center",
    marginBottom: "40px",
  },
  headerSmall: {
    fontSize: "12px",
    letterSpacing: "2px",
    color: "#666",
    marginBottom: "10px",
  },
  headerTitle: {
    fontSize: "42px",
    fontWeight: "bold",
    color: "#1a1a1a",
    margin: 0,
  },
  highlight: {
    color: "#6FB3FF",
  },
  grid: {
    display: "grid",
    gridTemplateColumns: "repeat(5, 1fr)",
    gridAutoRows: "280px",
  },
  card: {
    padding: "25px",
    // borderRadius: "12px",
    position: "relative",
    overflow: "hidden",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
  },
  cardLarge: {
    gridColumn: "span 2",
    gridRow: "span 2",
  },
  cardTag: {
    fontSize: "11px",
    letterSpacing: "1.5px",
    fontWeight: "bold",
    marginBottom: "15px",
    color: "#333",
  },
  cardTitle: {
    fontSize: "18px",
    fontWeight: "bold",
    lineHeight: "1.4",
    margin: "0 0 auto 0",
    color: "#333",
  },
  cardTitleLarge: {
    fontSize: "32px",
    fontWeight: "bold",
    lineHeight: "1.3",
    color: "#fff",
    margin: "auto 0",
  },
  cardSubtitle: {
    fontSize: "14px",
    lineHeight: "1.5",
    marginTop: "10px",
    color: "#333",
  },
  cardDate: {
    fontSize: "13px",
    marginTop: "15px",
    color: "#333",
  },
  iconButton: {
    width: "40px",
    height: "40px",
    borderRadius: "50%",
    border: "2px solid #fff",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color: "#fff",
    fontSize: "20px",
    marginTop: "20px",
    cursor: "pointer",
  },
  logoContainer: {
    margin: "20px 0",
  },
  logo: {
    display: "flex",
    gap: "5px",
    alignItems: "center",
  },
  triangle: {
    width: 0,
    height: 0,
    borderTop: "15px solid transparent",
    borderBottom: "15px solid transparent",
  },
  seamEduLogo: {
    display: "flex",
    flexDirection: "column",
    gap: "5px",
    margin: "20px 0",
  },
  playButton: {
    position: "absolute",
    top: "20px",
    right: "20px",
    width: "40px",
    height: "40px",
    borderRadius: "50%",
    backgroundColor: "#FFB800",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color: "#fff",
    fontSize: "14px",
    cursor: "pointer",
  },
  orangeLine: {
    width: "60px",
    height: "3px",
    backgroundColor: "#FF8C00",
    marginBottom: "20px",
  },
};
