"use client";
import Image from "next/image";
import styles from "./SixthSection.module.css";

const testimonialsData = [
  {
    name: "NAMAN SUKHIJA",
    batch: "B. Tech (CE), 2012-16 Batch",
    role: "Designer & Certifier, Passive House Institute, Germany",
    img: "/images/home-page/sixth-section-first-banner.png",
    quote: "Studying at JSS Noida has been a transformative journey...",
  },
  {
    name: "APOORV SHIKHAR",
    batch: "B. Tech (ES), 2013-17 Batch",
    role: "Junior Research Fellow, GB Pant National Institute of Himalayan Environment, Uttarakhand",
    img: "/images/home-page/sixth-section-fifth-banner.png",
    quote:
      "I have gained both theoretical and practical knowledge and was exposed to real-world challenges.",
  },
  {
    name: "YASHIKA MATHUR",
    batch: "B. Tech (CE), 2013-17 Batch",
    role: "Counsellor – Engineering Design, Design2Occupancy Services LLP",
    img: "/images/home-page/sixth-section-third-banner.png",
    // video: true,
  },
  {
    name: "NADEEM KHAN",
    batch: "B. Tech (ME), 2015-19 Batch",
    role: "Project Mechanical Engineer, JK Paper Ltd, Unit CPM, Gujarat",
    img: "/images/home-page/sixth-section-fourth-banner.png",
    // video: true,
  },
  {
    name: "RITIKA SHARMA",
    batch: "B. Tech (CSE), Batch of 2024",
    role: "",
    img: "/images/home-page/sixth-section-fifth-banner.png",
    quote:
      "JSS University has truly shaped my journey—both academically and personally.",
  },
];

export default function TestimonialsSection() {
  const [naman, apoorv, yashika, nadeem, ritika] = testimonialsData;

  return (
    <section className={styles.testimonialsSection}>
      <div className={styles.testimonialsContainer}>
        {/* LEFT COLUMN - Header + Large Card */}
        <div className={`${styles.column} ${styles.columnLeft}`}>
          <div className={styles.headerContent}>
            <p className={styles.testimonialsLabel}>TESTIMONIALS</p>
            <h2 className={styles.mainHeading}>
              IN <span className={styles.blueText}>CONVERSATION</span>
              <br />
              WITH THE{" "}
              <span className={styles.blueText}>
                JSS
                <br />
                COMMUNITY
              </span>
            </h2>
            <button className={styles.circleArrowBtn}>
              <svg
                width="20"
                height="20"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
              >
                <path d="M5 12h14M12 5l7 7-7 7" />
              </svg>
            </button>
          </div>

          <article className={`${styles.testimonialCard} ${styles.cardNaman}`}>
            <div className={styles.cardImgContainer}>
              <Image
                src={naman.img}
                alt={naman.name}
                width={380}
                height={520}
                className={styles.cardImg}
              />
              <div className={styles.quoteBar}>
                <div className={styles.yellowLine}></div>
                <p className={styles.quoteTextOverlay}>{naman.quote}</p>
              </div>
            </div>
            <div className={styles.cardInfo}>
              <h3 className={styles.personName}>{naman.name}</h3>
              <p className={styles.personBatch}>{naman.batch}</p>
              <p className={styles.personRole}>{naman.role}</p>
            </div>
          </article>
        </div>

        {/* MIDDLE COLUMN - Two Cards */}
        <div className={`${styles.column} ${styles.columnMiddle}`}>
          <article
            className={`${styles.testimonialCard} ${styles.cardYashika}`}
          >
            <div className={styles.cardImgContainer}>
              <Image
                src={yashika.img}
                alt={yashika.name}
                width={340}
                height={280}
                className={styles.cardImg}
              />
              {yashika.video && (
                <div className={styles.playBtn}>
                  <svg
                    width="14"
                    height="14"
                    viewBox="0 0 24 24"
                    fill="currentColor"
                  >
                    <path d="M8 5v14l11-7z" />
                  </svg>
                </div>
              )}
            </div>
            <div className={styles.cardInfo}>
              <h3 className={styles.personName}>{yashika.name}</h3>
              <p className={styles.personBatch}>{yashika.batch}</p>
              <p className={styles.personRole}>{yashika.role}</p>
            </div>
          </article>
          <article
            className={`${styles.testimonialCard} ${styles.cardYashika}`}
          >
            <div className={styles.cardImgContainer}>
              <Image
                src={yashika.img}
                alt={yashika.name}
                width={340}
                height={280}
                className={styles.cardImg}
              />
              {yashika.video && (
                <div className={styles.playBtn}>
                  <svg
                    width="14"
                    height="14"
                    viewBox="0 0 24 24"
                    fill="currentColor"
                  >
                    <path d="M8 5v14l11-7z" />
                  </svg>
                </div>
              )}
            </div>
            <div className={styles.cardInfo}>
              <h3 className={styles.personName}>{yashika.name}</h3>
              <p className={styles.personBatch}>{yashika.batch}</p>
              <p className={styles.personRole}>{yashika.role}</p>
            </div>
          </article>
        </div>

        {/* RIGHT COLUMN - Two Cards */}
        <div className={`${styles.column} ${styles.columnRight}`}>
          <article className={`${styles.testimonialCard} ${styles.cardNadeem}`}>
            <div className={styles.cardImgContainer}>
              <Image
                src={nadeem.img}
                alt={nadeem.name}
                width={380}
                height={380}
                className={styles.cardImg}
              />
              {nadeem.video && (
                <div className={styles.playBtn}>
                  <svg
                    width="14"
                    height="14"
                    viewBox="0 0 24 24"
                    fill="currentColor"
                  >
                    <path d="M8 5v14l11-7z" />
                  </svg>
                </div>
              )}
            </div>
            <div className={styles.cardInfo}>
              <h3 className={styles.personName}>{nadeem.name}</h3>
              <p className={styles.personBatch}>{nadeem.batch}</p>
              <p className={styles.personRole}>{nadeem.role}</p>
            </div>
          </article>

          <article className={`${styles.testimonialCard} ${styles.cardRitika}`}>
            <div className={styles.cardImgContainer}>
              <Image
                src={ritika.img}
                alt={ritika.name}
                width={340}
                height={280}
                className={styles.cardImg}
              />
              <div className={`${styles.quoteBar} ${styles.bottomQuote}`}>
                <div className={styles.yellowLine}></div>
                <p className={styles.quoteTextOverlay}>{ritika.quote}</p>
              </div>
            </div>
            <div className={styles.cardInfo}>
              <h3 className={styles.personName}>{ritika.name}</h3>
              <p className={styles.personBatch}>{ritika.batch}</p>
            </div>
          </article>
        </div>
      </div>
    </section>
  );
}
